/**
 * Signup Page - Inscription d'un nouvel tablissement
 * Formulaire amlior pour les directeurs d'tablissement
 */

import { useState, useEffect, useMemo, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { supabase } from '../../lib/supabase';
import {
  GraduationCap,
  School,
  User,
  Mail,
  Lock,
  Phone,
  MapPin,
  Globe,
  Check,
  AlertCircle,
  Eye,
  EyeOff,
  ArrowLeft,
  Sparkles
} from 'lucide-react';

export default function SignupPage() {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [step, setStep] = useState(1); // 1: Info cole, 2: Info directeur, 3: Classes
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);

  const [formData, setFormData] = useState({
    schoolName: '',
    directorName: '',
    email: '',
    password: '',
    confirmPassword: '',
    phone: '',
    address: '',
    schoolType: '',
    country: '',
    city: '',
    availableClasses: []
  });

  // Donnes des pays et villes
  const countryData = {
    'cameroon': {
      label: 'Cameroun',
      phoneCode: '+237',
      cities: ['Yaound', 'Douala', 'Bamenda', 'Bafoussam', 'Garoua', 'Maroua', 'Ngaoundr', 'Bertoua', 'Ebolowa', 'Kumba']
    },
    'france': {
      label: 'France',
      phoneCode: '+33',
      cities: ['Paris', 'Marseille', 'Lyon', 'Toulouse', 'Nice', 'Nantes', 'Strasbourg', 'Montpellier', 'Bordeaux', 'Lille']
    },
    'senegal': {
      label: 'Sngal',
      phoneCode: '+221',
      cities: ['Dakar', 'This', 'Kaolack', 'Ziguinchor', 'Saint-Louis', 'Tambacounda', 'Mbour', 'Diourbel']
    }
  };

  // Types d'tablissements
  const schoolTypes = [
    { value: 'primary', label: 'cole Primaire', description: 'CP1  CM2', icon: '' },
    { value: 'college', label: 'Collge', description: '6me  3me', icon: '' },
    { value: 'high_school', label: 'Lyce', description: '2nd  Terminale', icon: '' },
    { value: 'secondary', label: 'Secondaire Complet', description: '6me  Terminale', icon: '' },
    { value: 'institut', label: 'Institut', description: 'BTS, DUT', icon: '' },
    { value: 'university', label: 'Universit', description: 'Licence, Master, Doctorat', icon: '' }
  ];

  // Classes disponibles selon le type
  const getAvailableClassesByType = (schoolType) => {
    switch (schoolType) {
      case 'primary':
        return [
          { value: 'CP1', label: 'CP1 (Cours Prparatoire 1)', category: 'primaire' },
          { value: 'CP2', label: 'CP2 (Cours Prparatoire 2)', category: 'primaire' },
          { value: 'CE1', label: 'CE1 (Cours lmentaire 1)', category: 'primaire' },
          { value: 'CE2', label: 'CE2 (Cours lmentaire 2)', category: 'primaire' },
          { value: 'CM1', label: 'CM1 (Cours Moyen 1)', category: 'primaire' },
          { value: 'CM2', label: 'CM2 (Cours Moyen 2)', category: 'primaire' }
        ];
      case 'college':
        return [
          { value: '6me', label: '6me', category: 'collge' },
          { value: '5me', label: '5me', category: 'collge' },
          { value: '4me', label: '4me', category: 'collge' },
          { value: '3me', label: '3me', category: 'collge' }
        ];
      case 'high_school':
        return [
          { value: '2nd', label: '2nd (Seconde)', category: 'lyce' },
          { value: '1re', label: '1re (Premire)', category: 'lyce' },
          { value: 'Terminale', label: 'Terminale', category: 'lyce' }
        ];
      case 'secondary':
        return [
          { value: '6me', label: '6me', category: 'collge' },
          { value: '5me', label: '5me', category: 'collge' },
          { value: '4me', label: '4me', category: 'collge' },
          { value: '3me', label: '3me', category: 'collge' },
          { value: '2nd', label: '2nd (Seconde)', category: 'lyce' },
          { value: '1re', label: '1re (Premire)', category: 'lyce' },
          { value: 'Terminale', label: 'Terminale', category: 'lyce' }
        ];
      case 'institut':
        return [
          { value: 'BTS1', label: 'BTS 1re anne', category: 'suprieur' },
          { value: 'BTS2', label: 'BTS 2me anne', category: 'suprieur' },
          { value: 'DUT1', label: 'DUT 1re anne', category: 'suprieur' },
          { value: 'DUT2', label: 'DUT 2me anne', category: 'suprieur' }
        ];
      case 'university':
        return [
          { value: 'L1', label: 'Licence 1', category: 'universit' },
          { value: 'L2', label: 'Licence 2', category: 'universit' },
          { value: 'L3', label: 'Licence 3', category: 'universit' },
          { value: 'M1', label: 'Master 1', category: 'universit' },
          { value: 'M2', label: 'Master 2', category: 'universit' },
          { value: 'Doctorat', label: 'Doctorat', category: 'universit' }
        ];
      default:
        return [];
    }
  };

  // Mettre  jour les classes disponibles quand le type change
  useEffect(() => {
    if (!formData.schoolType) return;

    const availableClasses = getAvailableClassesByType(formData.schoolType);
    setFormData(prev => ({
      ...prev,
      availableClasses: availableClasses.map(cls => ({
        level: cls.value,
        isActive: false,
        category: cls.category,
        label: cls.label
      }))
    }));
  }, [formData.schoolType]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    setError(null);
  };

  const handleClassToggle = useCallback((classLevel) => {
    setFormData(prev => {
      if (!prev.availableClasses || prev.availableClasses.length === 0) return prev;

      const updatedClasses = prev.availableClasses.map(cls =>
        cls && cls.level === classLevel
          ? { ...cls, isActive: !cls.isActive }
          : cls
      );

      return { ...prev, availableClasses: updatedClasses };
    });
    setError(null);
  }, []);

  // Classes groupes par catgorie
  const categorizedClasses = useMemo(() => {
    if (!formData.availableClasses || formData.availableClasses.length === 0) return [];

    const validClasses = formData.availableClasses.filter(cls =>
      cls && cls.category && cls.level && typeof cls.category === 'string' && typeof cls.level === 'string'
    );

    const categories = [...new Set(validClasses.map(cls => cls.category))];
    return categories.map(category => ({
      category,
      classes: validClasses.filter(cls => cls.category === category)
    }));
  }, [formData.availableClasses]);

  const getSelectedClassesCount = () => {
    if (!formData.availableClasses || formData.availableClasses.length === 0) return 0;
    return formData.availableClasses.filter(cls => cls.isActive).length;
  };

  const validateStep = () => {
    if (step === 1) {
      if (!formData.schoolName || !formData.schoolType || !formData.country || !formData.city || !formData.address) {
        setError('Veuillez remplir tous les champs de l\'tablissement');
        return false;
      }
    } else if (step === 2) {
      if (!formData.directorName || !formData.email || !formData.phone || !formData.password || !formData.confirmPassword) {
        setError('Veuillez remplir tous les champs du directeur');
        return false;
      }
      if (formData.password !== formData.confirmPassword) {
        setError('Les mots de passe ne correspondent pas');
        return false;
      }
      if (formData.password.length < 8) {
        setError('Le mot de passe doit contenir au moins 8 caractres');
        return false;
      }
    } else if (step === 3) {
      if (getSelectedClassesCount() === 0) {
        setError('Veuillez slectionner au moins une classe');
        return false;
      }
    }
    setError(null);
    return true;
  };

  const handleNext = () => {
    if (validateStep()) {
      setStep(step + 1);
    }
  };

  const handleBack = () => {
    setStep(step - 1);
    setError(null);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validateStep()) return;

    setLoading(true);
    setError(null);

    try {
      const selectedClasses = formData.availableClasses
        .filter(cls => cls.isActive)
        .map(cls => cls.level);

      // Gnrer un code unique pour l'cole
      const prefix = formData.schoolName.replace(/\s+/g, '').substring(0, 3).toUpperCase();
      const year = new Date().getFullYear();
      const random = Math.floor(Math.random() * 999).toString().padStart(3, '0');
      const schoolCode = `${prefix}-${year}-${random}`;

      // Crer l'utilisateur avec Supabase Auth
      const { data: authData, error: authError } = await supabase.auth.signUp({
        email: formData.email,
        password: formData.password,
        options: {
          data: {
            role: 'principal',
            full_name: formData.directorName,
            phone: formData.phone,
            school: {
              name: formData.schoolName,
              code: schoolCode,
              type: formData.schoolType,
              phone: formData.phone,
              address: formData.address,
              city: formData.city,
              country: formData.country,
              available_classes: selectedClasses
            }
          }
        }
      });

      if (authError) throw authError;

      if (!authData.user) {
        throw new Error('Erreur lors de la cration de l\'utilisateur');
      }

      // Rediriger vers la page de vrification email
      navigate('/email-verification', { state: { email: formData.email } });

    } catch (error) {
      console.error(' Erreur lors de l\'inscription:', error);
      setError(error.message || 'Une erreur est survenue lors de l\'inscription');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 to-primary-100">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 shadow-sm">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <button
              onClick={() => navigate('/')}
              className="flex items-center gap-2 text-gray-600 hover:text-gray-900 transition-colors"
            >
              <ArrowLeft className="h-5 w-5" />
              <span className="font-medium">Retour</span>
            </button>

            <div className="flex items-center gap-2">
              <GraduationCap className="h-6 w-6 text-primary-600" />
              <span className="text-xl font-bold text-gray-900">EduTrack</span>
            </div>

            <div className="text-sm text-gray-600">
              Dj un compte ? <button onClick={() => navigate('/login')} className="text-primary-600 hover:text-primary-700 font-medium">Connexion</button>
            </div>
          </div>
        </div>
      </div>

      {/* Progress Steps */}
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="flex items-center justify-between mb-8">
          {[
            { num: 1, label: 'tablissement', icon: School },
            { num: 2, label: 'Directeur', icon: User },
            { num: 3, label: 'Classes', icon: GraduationCap }
          ].map((stepItem, idx) => {
            const Icon = stepItem.icon;
            const isActive = step === stepItem.num;
            const isCompleted = step > stepItem.num;

            return (
              <div key={stepItem.num} className="flex items-center flex-1">
                <div className={`flex items-center gap-3 flex-1 ${idx < 2 ? 'pr-4' : ''}`}>
                  <div className={`
                    flex items-center justify-center w-12 h-12 rounded-full border-2 transition-all
                    ${isCompleted ? 'bg-green-500 border-green-500 text-white' : ''}
                    ${isActive ? 'bg-primary-600 border-primary-600 text-white' : ''}
                    ${!isActive && !isCompleted ? 'bg-white border-gray-300 text-gray-400' : ''}
                  `}>
                    {isCompleted ? <Check className="h-6 w-6" /> : <Icon className="h-6 w-6" />}
                  </div>
                  <div className="flex-1">
                    <div className={`text-sm font-medium ${isActive ? 'text-primary-600' : isCompleted ? 'text-green-600' : 'text-gray-500'}`}>
                      tape {stepItem.num}
                    </div>
                    <div className={`text-xs ${isActive ? 'text-gray-900' : 'text-gray-600'}`}>
                      {stepItem.label}
                    </div>
                  </div>
                </div>
                {idx < 2 && (
                  <div className={`h-0.5 w-full transition-all ${step > stepItem.num ? 'bg-green-500' : 'bg-gray-300'}`}></div>
                )}
              </div>
            );
          })}
        </div>

        {/* Form Container */}
        <div className="bg-white rounded-2xl shadow-xl p-8 md:p-12">
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Error Alert */}
            {error && (
              <div className="bg-red-50 border border-red-200 rounded-lg p-4 flex items-start gap-3">
                <AlertCircle className="h-5 w-5 text-red-600 flex-shrink-0 mt-0.5" />
                <p className="text-sm text-red-800">{error}</p>
              </div>
            )}

            {/* tape 1 : Informations tablissement */}
            {step === 1 && (
              <div className="space-y-6">
                <div className="text-center mb-8">
                  <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-primary-100 text-primary-600 mb-4">
                    <School className="h-8 w-8" />
                  </div>
                  <h2 className="text-2xl font-bold text-gray-900">Informations sur l'tablissement</h2>
                  <p className="text-gray-600 mt-2">Commenons par les dtails de votre cole</p>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Nom de l'tablissement *
                  </label>
                  <input
                    type="text"
                    name="schoolName"
                    value={formData.schoolName}
                    onChange={handleChange}
                    className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
                    placeholder="Ex: Collge de la Russite"
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Type d'tablissement *
                  </label>
                  <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
                    {schoolTypes.map((type) => (
                      <button
                        key={type.value}
                        type="button"
                        onClick={() => {
                          setFormData(prev => ({ ...prev, schoolType: type.value }));
                          setError(null);
                        }}
                        className={`p-4 rounded-lg border-2 transition-all text-left ${
                          formData.schoolType === type.value
                            ? 'border-primary-500 bg-primary-50'
                            : 'border-gray-200 hover:border-gray-300 bg-white'
                        }`}
                      >
                        <div className="text-2xl mb-2">{type.icon}</div>
                        <div className="font-medium text-gray-900 text-sm">{type.label}</div>
                        <div className="text-xs text-gray-500 mt-1">{type.description}</div>
                      </button>
                    ))}
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      <Globe className="inline h-4 w-4 mr-1" />
                      Pays *
                    </label>
                    <select
                      name="country"
                      value={formData.country}
                      onChange={handleChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
                      required
                    >
                      <option value="">Slectionner un pays</option>
                      {Object.keys(countryData).map(key => (
                        <option key={key} value={key}>{countryData[key].label}</option>
                      ))}
                    </select>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      <MapPin className="inline h-4 w-4 mr-1" />
                      Ville *
                    </label>
                    <select
                      name="city"
                      value={formData.city}
                      onChange={handleChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
                      required
                      disabled={!formData.country}
                    >
                      <option value="">Slectionner une ville</option>
                      {formData.country && countryData[formData.country]?.cities.map(city => (
                        <option key={city} value={city}>{city}</option>
                      ))}
                    </select>
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Adresse complte *
                  </label>
                  <textarea
                    name="address"
                    value={formData.address}
                    onChange={handleChange}
                    className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
                    placeholder="Adresse complte de l'tablissement"
                    rows={2}
                    required
                  />
                </div>

                <button
                  type="button"
                  onClick={handleNext}
                  className="w-full px-6 py-3 bg-primary-600 text-white rounded-lg font-semibold hover:bg-primary-700 transition-colors"
                >
                  Continuer
                </button>
              </div>
            )}

            {/* tape 2 : Informations Directeur */}
            {step === 2 && (
              <div className="space-y-6">
                <div className="text-center mb-8">
                  <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-primary-100 text-primary-600 mb-4">
                    <User className="h-8 w-8" />
                  </div>
                  <h2 className="text-2xl font-bold text-gray-900">Compte Directeur</h2>
                  <p className="text-gray-600 mt-2">Crez votre compte d'accs administrateur</p>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Nom complet du directeur *
                  </label>
                  <input
                    type="text"
                    name="directorName"
                    value={formData.directorName}
                    onChange={handleChange}
                    className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
                    placeholder="Ex: Jean Dupont"
                    required
                  />
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      <Mail className="inline h-4 w-4 mr-1" />
                      Email *
                    </label>
                    <input
                      type="email"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
                      placeholder="directeur@ecole.com"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      <Phone className="inline h-4 w-4 mr-1" />
                      Tlphone *
                    </label>
                    <div className="flex gap-2">
                      <div className="w-24">
                        <input
                          type="text"
                          value={formData.country ? countryData[formData.country]?.phoneCode : ''}
                          className="w-full px-3 py-3 border border-gray-300 rounded-lg bg-gray-50 text-gray-600 text-center"
                          disabled
                        />
                      </div>
                      <input
                        type="tel"
                        name="phone"
                        value={formData.phone}
                        onChange={handleChange}
                        className="flex-1 px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
                        placeholder="XXXXXXXXX"
                        required
                      />
                    </div>
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      <Lock className="inline h-4 w-4 mr-1" />
                      Mot de passe *
                    </label>
                    <div className="relative">
                      <input
                        type={showPassword ? 'text' : 'password'}
                        name="password"
                        value={formData.password}
                        onChange={handleChange}
                        className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 pr-12"
                        placeholder="Minimum 8 caractres"
                        required
                      />
                      <button
                        type="button"
                        onClick={() => setShowPassword(!showPassword)}
                        className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-500 hover:text-gray-700"
                      >
                        {showPassword ? <EyeOff className="h-5 w-5" /> : <Eye className="h-5 w-5" />}
                      </button>
                    </div>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Confirmer le mot de passe *
                    </label>
                    <div className="relative">
                      <input
                        type={showConfirmPassword ? 'text' : 'password'}
                        name="confirmPassword"
                        value={formData.confirmPassword}
                        onChange={handleChange}
                        className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500 pr-12"
                        placeholder="Confirmer le mot de passe"
                        required
                      />
                      <button
                        type="button"
                        onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                        className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-500 hover:text-gray-700"
                      >
                        {showConfirmPassword ? <EyeOff className="h-5 w-5" /> : <Eye className="h-5 w-5" />}
                      </button>
                    </div>
                  </div>
                </div>

                <div className="flex gap-4">
                  <button
                    type="button"
                    onClick={handleBack}
                    className="flex-1 px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-semibold hover:bg-gray-50 transition-colors"
                  >
                    Retour
                  </button>
                  <button
                    type="button"
                    onClick={handleNext}
                    className="flex-1 px-6 py-3 bg-primary-600 text-white rounded-lg font-semibold hover:bg-primary-700 transition-colors"
                  >
                    Continuer
                  </button>
                </div>
              </div>
            )}

            {/* tape 3 : Slection des Classes */}
            {step === 3 && (
              <div className="space-y-6">
                <div className="text-center mb-8">
                  <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-primary-100 text-primary-600 mb-4">
                    <GraduationCap className="h-8 w-8" />
                  </div>
                  <h2 className="text-2xl font-bold text-gray-900">Classes Disponibles</h2>
                  <p className="text-gray-600 mt-2">Slectionnez les classes de votre tablissement</p>
                  <div className="mt-4 inline-flex items-center gap-2 px-4 py-2 bg-green-50 text-green-700 rounded-full text-sm font-medium">
                    <Check className="h-4 w-4" />
                    <span>{getSelectedClassesCount()} classe{getSelectedClassesCount() > 1 ? 's' : ''} slectionne{getSelectedClassesCount() > 1 ? 's' : ''}</span>
                  </div>
                </div>

                {categorizedClasses.map(({ category, classes }) => (
                  <div key={category} className="space-y-3">
                    <h3 className="text-lg font-semibold text-gray-900 capitalize">{category}</h3>
                    <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
                      {classes.map((cls) => (
                        <button
                          key={cls.level}
                          type="button"
                          onClick={() => handleClassToggle(cls.level)}
                          className={`p-4 rounded-lg border-2 transition-all text-left ${
                            cls.isActive
                              ? 'border-primary-500 bg-primary-50'
                              : 'border-gray-200 hover:border-gray-300 bg-white'
                          }`}
                        >
                          <div className="flex items-center justify-between">
                            <div className="font-medium text-gray-900">{cls.level}</div>
                            {cls.isActive && (
                              <div className="w-6 h-6 rounded-full bg-primary-600 flex items-center justify-center">
                                <Check className="h-4 w-4 text-white" />
                              </div>
                            )}
                          </div>
                          <div className="text-xs text-gray-500 mt-1">{cls.label}</div>
                        </button>
                      ))}
                    </div>
                  </div>
                ))}

                <div className="flex gap-4 pt-6">
                  <button
                    type="button"
                    onClick={handleBack}
                    className="flex-1 px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-semibold hover:bg-gray-50 transition-colors"
                  >
                    Retour
                  </button>
                  <button
                    type="submit"
                    disabled={loading}
                    className="flex-1 px-6 py-3 bg-gradient-to-r from-primary-600 to-primary-700 text-white rounded-lg font-semibold hover:from-primary-700 hover:to-primary-800 transition-all disabled:opacity-50 disabled:cursor-not-allowed inline-flex items-center justify-center gap-2"
                  >
                    {loading ? (
                      <>
                        <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                        <span>Cration en cours...</span>
                      </>
                    ) : (
                      <>
                        <Sparkles className="h-5 w-5" />
                        <span>Crer Mon Compte</span>
                      </>
                    )}
                  </button>
                </div>
              </div>
            )}
          </form>
        </div>
      </div>
    </div>
  );
}
